
.Projets
{
    background-color: #474B4F;
    .GallerieProjets
    {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        

        box-sizing: border-box;
        padding-top: 200px;
        //margin: 5em 2em;

        li:nth-child(even)
        {
            background-color: #222629;
            h2
            {
                color: #61892F;
            }
        }
        li:nth-child(odd)
        {
            background-color: #474B4F;
            h2
            {
                color: #61892F;
            }
        }
    }
}
